@import 'assets/styles/index.scss';

.step {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 12px;
  padding: 4px;
  border: 1px solid #eee;
}
.stepTitle {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  font-size: 14px;
  color: #888;
}
.stepRow {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;

  @media (min-width: 700px) {
    flex-direction: row;
  }
}
.stepRowInput {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 150px;
}
.stepRowInputP {
  margin-left: 8px;
  text-align: left;
  font-size: 12px;
  font-weight: 300;
}
.stepRowText {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  @media (min-width: 700px) {
    margin-left: 18px;
  }
}
.stepRowTextTitle {
  margin: 8px 0 4px 0;
  text-align: left;
  color: #555;
  font-size: 14px;
  font-weight: 500;
}
.stepRowTextP {
  text-align: left;
  font-size: 12px;
  font-weight: 300;
}

.stepGreen {
  @extend .step;
  background-color: #008800;
  & p {
    font-size: 18px;
    font-weight: 500;
    color: #fff;
  }
}
.stepRed {
  @extend .step;
  background-color: #880000;
  & p {
    font-size: 18px;
    font-weight: 500;
    color: #fff;
  }
}

.setSirens {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 12px 0;
  width: 100%;

  & .setSirensBtn {
    margin: 0;
    padding: 4px;
    font-weight: 500;
    color: #fff;
    border: 1px solid #333;
    border-radius: 4px;
    background-color: #09af00;
  }

  & .cancelBtn {
    margin: 0;
    padding: 4px;
    font-weight: 500;
    color: #333;
    border: 1px solid #333;
    border-radius: 4px;
    background-color: #eee;
  }
}

.setSirensText {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  font-size: 14px;
  color: #555;
}

.selectSerene {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 0px;
  margin: 0;
  overflow: hidden;
  transition: height 0.4s ease-in-out;
}
.selectSereneShow {
  height: 330px;

  @media (min-width: 610px) {
    height: 230px;
  }
  @media (min-width: 920px) {
    height: 140px;
  }
  @media (min-width: 920px) {
    height: 140px;
  }
}
