@import 'assets/styles/fonts.scss';

.main {
  position: absolute;
  width: 40px;

  user-select: none;

  & .img {
    width: 40px;
  }

  & .text {
    position: absolute;
    top: 0;
    left: 45px;
    width: 60px;
  }
  & .textLeft {
    position: absolute;
    top: 0;
    right: 45px;
    width: 60px;
  }
}

.soundActive {
  position: absolute;
  z-index: -1;
  top: 30px;
  left: 0;
  right: 0;
  bottom: 30px;
}
.soundActive::before,
.soundActive::after {
  content: '';
  position: absolute;
  border: 2px solid #09af00;
  left: -20px;
  right: -20px;
  top: -20px;
  bottom: -20px;
  border-radius: 50%;
  animation: pulse 1.8s linear infinite;
  opacity: 0;
}
.soundActive::after {
  animation-delay: 0.5s;
}

@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}
