@import 'assets/styles/index.scss';

.main {
    position: absolute;

    .btn{
        @extend .materialBlock;
        @extend .materialBlockHover;

        width: 100%;
        border: 1px solid #333;
        border-radius: 4px;
    }
    .green{
        color: #fff;
        background-color: #09af00;
    }
    .orange{
        color: #fff;
        background-color: #ee6002;
    }
    .red{
        color: #fff;
        background-color: #ff0000;
    }
}
