@import 'assets/styles/fonts.scss';

.main {
  display: flex;
  flex-direction: column;

  width: 90%;
  max-width: 550px;
  min-height: 140px;

  background-color: #fff;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  width: 100%;
  padding: 4px 8px;
  border-bottom: 2px solid #aaa;

  box-sizing: border-box;
}

.header_title {
  color: #888;
}

.header_closeIcon {
  cursor: pointer;
  width: 18px;
  height: 18px;
}

.body {
  width: 100%;
  padding: 12px 8px 8px 8px;

  box-sizing: border-box;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  margin-bottom: 8px;
}
