@import 'assets/styles/index.scss';

.main{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 150px;
    height: 150px;
    padding: 12px 4px 4px 4px;
}

.micControls{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 150px;
}

.controls{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 150px;

    &_itemLEDs{
        display: flex;
        align-items: flex-start;
        height: 40px;
    }
    &_item{
        display: flex;
        align-items: flex-end;
        height: 40px;
    }
}
