@import 'assets/styles/index.scss';

.main{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
}

.text1{
    position: absolute;
    font-size: 11px;
    bottom: 40px;
    &::before{
        content: "";
        position: absolute;
        left:50%;
        bottom: -5px;
        width: 5px;
        height: 5px;
        background-color: #333;
        border-radius: 50%;
        transform: translateX(-30%)
    }
}
.text2{
    position: absolute;
    top: 50%;
    right: 45px;
    font-size: 11px;
    transform: translateY(-50%);
    &::before{
        content: "";
        position: absolute;
        top: 50%;
        right:-10px;
        width: 5px;
        height: 5px;
        background-color: #333;
        border-radius: 50%;
        transform: translateY(-30%);
    }
}
.text4{
    position: absolute;
    bottom: -16px;
    right: -14px;
    font-size: 11px;
    transform: translateY(-50%);
}

.key{
    position: relative;
    width: 30px;
    height: 30px;
}

.circles{
    width: 30px;
    height: 30px;
    border: 1px solid #333;
    border-radius: 50%;

    box-shadow:
        inset  0px 0px 0 3px #fff,
        inset  0px 0px 0 4px #333,
        inset  0px 0px 0 15px #fff,
        inset  0px 0px 0 16px #333;
}

.key_item{
    cursor: pointer;
    position: absolute;
    top: 12px;
    left: -5px;
    width: 40px;
    height: 6px;
    border: 1px solid #333;
    background-color: #ccc;
    transition: transform 0.2s linear;
}
.on{
    transform: rotate(90deg);
}
