@import 'assets/styles/fonts.scss';

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.settings {
  display: flex;
  align-items: center;
  margin: 8px;

  .button {
    margin: 0 8px;
  }
}

.symbol {
  display: flex;
  justify-content: center;

  .square {
    cursor: pointer;
    width: 40px;
    height: 40px;
    margin: 4px;
    border: 1px solid #888;
    box-sizing: border-box;
  }

  .squareActive {
    @extend .square;

    background-color: #333;
  }
}
