@import 'assets/styles/fonts.scss';

.main{
    display: flex;
    align-items: center;
}

.led{
    width: 10px;
    height: 10px;
    border: 1px solid #333;
    border-radius: 50%;
}
.red{
    background-color: #ffd6d6;

    &.on{
        background-color: #ff0000;
        box-shadow: 0px 0px 10px rgba(255,0,0,0.85);
    }
}
.green{
    background-color: #d6ffd6;

    &.on{
        background-color: #00ff00;
        box-shadow: 0px 0px 10px rgba(0,255,0,0.85);
    }
}
.yellow{
    background-color: #ffffd6;

    &.on{
        background-color: #ffae00;
        box-shadow: 0px 0px 10px rgba(255,255,0,0.85);
    }
}


.text{
    margin-left: 4px;
    font-size: 12px;
    font-weight: 500;
}
