@import 'assets/styles/index.scss';

.main {
  width: 100%;
  margin: 0;
  padding: 0 12px 12px 12px;
  box-sizing: border-box;
}

.title {
  margin-bottom: 12px;
}
.panelSelect {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  @media (min-width: 600px) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.selectItem {
  margin: 12px 0;
  width: 100%;

  @media (min-width: 600px) {
    width: 260px;
    margin-right: 12px;
  }
}

.panelBtn {
  margin-bottom: 8px;
  & .setSirensBtn {
    margin: 0 8px 0 0;
    padding: 4px;
    border: 1px solid #333;
    border-radius: 4px;
    background-color: #09af00;

    & p {
      color: #fff;
      font-weight: 600;
    }
  }

  & .resetBtn {
    margin: 0;
    padding: 4px;
    font-weight: 500;
    border: 1px solid #333;
    border-radius: 4px;
  }

  & .cancelBtn {
    margin-left: 8px;
  }
}

.spanText {
  padding-left: 4px;
  color: #888;
  font-size: 12px;
}

/*
old show table
*/

.row {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  &:hover {
    background-color: #aaeeaa;
  }
}

.rowHeader {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.cell {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 2px 0;
  border: 1px solid #aaa;
}

.cell1 {
  @extend .cell;
  width: 150px;
}
.cell2 {
  @extend .cell;
  width: 50px;
  border-left: none;
}
.cell3 {
  @extend .cell;
  width: 50px;
  border-left: none;
}
