@import 'assets/styles/vars.scss';

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.bodyPage {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  padding: 12px;
  box-sizing: border-box;
}

.isDesktop {
  width: calc(100% - 260px); // 260px = $ASIDE_MENU_WIDTH
  margin-left: $ASIDE_MENU_WIDTH; // margin for menu
}
