@import './reset.scss';
@import './fonts.scss';
@import './vars.scss';
@import './mixins.scss';

body{
    font-family: 'Roboto', sans-serif;
}

/* =================================
    ReactCSSTransitionGroup Classes
==================================== */
/* start enter state */
.translateX-enter {
  opacity: 0;
  transform: translateX(-100%);
}

/* end enter state */
.translateX-enter.translateX-enter-active {
  opacity: 1;
  // transition: opacity 0.5s ease-in;
  transform: translateX(0);
  transition: transform 0.5s ease-out;
}

/* start leave state */
.translateX-leave, // ReactCSSTransitionGroup
.translateX-exit { // react-transition-group
  opacity: 1;
  transform: translateX(0);

}

  /* end leave state */
.translateX-leave.translateX-leave-active,  // ReactCSSTransitionGroup
.translateX-exit-active {                   // react-transition-group
  transform: translateX(100%);
  // opacity: 0;
  transition: transform 0.3s ease-in;
}

/* initial mounting */
.translateX-appear {
  opacity: 0;
}

.translateX-appear.translateX-appear-active{
  opacity: 1;
  transition: opacity 0.5s ease-in;
}
