@import 'assets/styles/vars.scss';

.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 1px solid #aaa;
}

.box {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}

.input {
  display: none;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  background-color: #fff;
  box-sizing: border-box;
}

.label {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  background-color: #fff;
  box-sizing: border-box;
}

.labelTitle {
  font-size: 12px;
  color: #333;
}

.boxIcon {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 10%;
  height: 34px;
}

.boxIcon_delete {
  width: 28px;
  height: 28px;
}
