@import 'assets/styles/fonts.scss';

.main {
    position: absolute;
    width: 500px;
    height: 500px;
    box-sizing: border-box;

    border: 1px solid #333;
    background-color: #eee;
    box-shadow:
    0px 1px 5px 0px rgba(0,0,0,0.2),
    0px 2px 2px 0px rgba(0,0,0,0.14),
    0px 3px 1px -2px rgba(0,0,0,0.12);
}

.panel_text {
    margin-top: 70px;
}

.panel{
    position: absolute;
    left: 12px;
    right: 12px;
    bottom: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 150px;
    padding: 4px;
    border: 1px solid #333;
    border-radius: 12px;
    box-shadow:
    0px 1px 5px 0px rgba(0,0,0,0.2),
    0px 2px 2px 0px rgba(0,0,0,0.14),
    0px 3px 1px -2px rgba(0,0,0,0.12);
}
.panel_control{
    flex-grow: 1;
}
.panel_LCD{
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    width: 100%;
    height: 100px;
}

.logo{
    position: absolute;
    top: 12px;
    left: 12px;
    width: 80px;
}
