@import 'assets/styles/fonts.scss';

.panelBtn {
  position: sticky;
  top: 0;
  z-index: 1200;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 12px 0;
  background-color: rgb(255, 255, 255);

  & .setSirensBtn {
    width: 30%;
    margin: 0;
    padding: 12px;
    font-size: 16px;
    border-radius: 4px;
    background-color: #09af00;

    & p {
      color: #fff;
      font-weight: 600;
    }
  }
}
