@import 'assets/styles/fonts.scss';
.main {
  margin-bottom: 40px;
}
.block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 20px 0;
}

.blockTitle {
  display: flex;
  margin: 4px 0;
  font-size: 18px;
  font-weight: 500;
}

.blockHelpTitle {
  display: flex;
  margin: 4px 0;
  color: #888;
  font-size: 12px;
  font-weight: 400;
}
.blockHelpTitleBold {
  display: flex;
  margin: 0 4px;

  color: #555;
  font-size: 12px;
  font-weight: 500;
}

.extraBlocks {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 8px;
}
.blockStep {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 8px;
}

.blockStepItem {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 8px;
}

.blockSirens {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 12px;

  & .resetBtn {
    margin: 0;
    padding: 4px;
    font-weight: 500;
    border-radius: 4px;
  }
}

.blockSirensItem {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 30%;
  margin-right: 16px;
}
