@import 'assets/styles/vars.scss';

.main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  z-index: 10000;
}

.layout {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding: 0;
  background-color: rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
}
